import { React, useCallback } from "react";
import { Link } from "react-router-dom";
import { motion } from "framer-motion";
import Particles from "react-tsparticles";
import { loadFull } from "tsparticles";
import { ArrowDown2, ArrowRight2 } from "iconsax-react";

import SocialInfo from "./SocialInfo";
import HomeParticles from "./HomeParticles";

import "./HomePage.scss";
import avatar from "./S__32448516.jpg";

function HomePage() {
  const particlesInit = useCallback(async (engine) => {
    console.log(engine);
    await loadFull(engine);
  }, []);

  const particlesLoaded = useCallback(async (container) => {
    await console.log(container);
  }, []);
  return (
    <>
      <motion.main
        className='main__container'
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        exit={{ opacity: 0 }}
        transition={{ duration: 1 }}
      >
        <div className='HomePage' id='HomePage'>
          <HomeParticles />
          <div className='main'>
            <div className='left_side'>
              <div className='avatar'>
                <img alt='my cat' src={avatar} />
              </div>
              <h1>Meowen</h1>
              <SocialInfo />
              <div className='my_title'>
                <div></div>
                <div></div>
                <div></div>
                <div></div>
                <div></div>
                <div></div>
              </div>
            </div>
            {/* <div className='right_side'>
              <pre></pre>
            </div> */}
          </div>
          <Link to='/AboutMe' className='Arrow ArrowDown'>
            <ArrowDown2 size='32' color='#a5a9aa' />
          </Link>
          <Link to='/Projects' className='Arrow ArrowRight'>
            <ArrowRight2 size='32' color='#a5a9aa' />
          </Link>
        </div>
      </motion.main>
      {/* <motion.div
        initial={{ scaleX: 2 }}
        animate={{ scaleX: 0, transition: { duration: 1, ease: "circOut" } }}
        exit={{ scaleX: 1, transition: { duration: 3, ease: "circIn" } }}
        style={{ originX: 0 }}
        className='privacy-screen'
      /> */}
    </>
  );
}

export default HomePage;
